import React, { useState } from "react";
import { post } from "axios";
import { useNavigate } from "react-router-dom";
import 'react-dropdown/style.css';
import Select from 'react-select';
import 'bootstrap/dist/css/bootstrap.min.css';


function CrudAdd(props) {
	// const initialState = {
	// 	companyName: "",
	// 	phone: "",
	// 	email: "",
	// 	location: "",
	// 	link: "",
	// 	description: "",
	// };
	const initialState = {
		BookName: "",
		Language: "",
		BookType: "",
	    BookAuther: "",
		NoCopiesActual: "",
		NoCopiesCurrent: "",
		PublicationYear: "",
		BookCover: "",
		BookDiscription: "",
		BookStatus: "",
		ISBN: "",
		
	};
	const bookStatus = [
		{ label: "Available", value: 1 },
		// { label: "Not Available", value: 2 },
	  ];
	
	const [bookData, setCrud] = useState(initialState);
	const navigate = useNavigate();
	function handleListChange (selectedOption) {
		bookData.BookStatus=selectedOption.label;
			  }
	function handleSubmit(event) {
		event.preventDefault();
		console.log("from api/cruds sagar",bookData)
		//if (!crud.companyName || !crud.email) return;
		async function saveBook() {
			try {
				const response = await post("http://localhost:8080/api/book",bookData);
				console.log("response 2 sagar", response);
				navigate("/books");
				// navigate(`/cruds/${response.data.BookInfoId}`);
			} catch (error) {
				console.log("error", error);
			}
		}
		saveBook();
	}

	
	function handleChange(event) {
		setCrud({ ...bookData, [event.target.name]: event.target.value });
		
	}

	function handleCancel() {
		navigate("/books");
	}

	
	
	return (
		<div className="container" style={{ maxWidth: "400px" }}>
			<h1>Add Book</h1>
			<hr />
			<form onSubmit={handleSubmit}>
			
				<div className="form-group">
					<label>Book Name</label>
					<input
						name="BookName"
						type="text"
						required
						value={bookData.BookName}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Language</label>
					<input
						name="Language"
						type="text"
						required
						value={bookData.Language}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Book Catagory</label>
					<input
						name="BookType"
						type="text"
						required
						value={bookData.BookType}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Auther</label>
					<input
						name="BookAuther"
						type="text"
						required
						value={bookData.BookAuther}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Number Of Copies Actual</label>
					<input
						name="NoCopiesActual"
						type="text"
						required
						value={bookData.NoCopiesActual}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Numbber Of Copies Current</label>
					<input
						name="NoCopiesCurrent"
						type="text"
						required
						value={bookData.NoCopiesCurrent}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Publication Year</label>
					<input
						name="PublicationYear"
						type="text"
						required
						value={bookData.PublicationYear}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Book Cover</label>
					<input
						name="BookCover"
						type="text"
						required
						value={bookData.BookCover}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Discription</label>
					<input
						name="BookDiscription"
						type="text"
						required
						value={bookData.BookDiscription}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
							<div className="form-group">
					<label>Book Status</label>
					{/* <input
						name="BookStatus"
						type="text"
						required
						value={bookData.BookStatus}
						onChange={handleChange}
						className="form-control"
					/> */}
					 <Select options={ bookStatus } 
					    name="BookStatus"
					  	required
					    onChange={handleListChange}
      					/>
					</div> 
				<div className="form-group">
					<label>ISBN</label>
					<input
						name="ISBN"
						type="text"
						required
						value={bookData.ISBN}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				
				<div className="btn-group">
					<input type="submit" value="Submit" className="btn btn-primary" />
					<button
						type="button"
						onClick={handleCancel}
						className="btn btn-secondary"
					>
						Cancel
					</button>
				</div>
			</form>
		</div>



	);
}

export default CrudAdd;
