import React, { useState, useEffect } from "react";
import { get, patch ,post} from "axios";
import { useNavigate, useParams } from "react-router-dom";
import DatePicker  from "react-datepicker";  
import "react-datepicker/dist/react-datepicker.css";  


function BookIssueDetails(props) {
	const initialState = {
		BookName: "",
		Language: "",
		BookType: "",
	    BookAuther: "",
		NoCopiesActual: "",
		NoCopiesCurrent: "",
		PublicationYear: "",
		BookCover: "",
		BookDiscription: "",
		BookStatus: "",
		ISBN: "",
			};

			//const bookIssueRecord = [];

            const bookIssueState = {
                BookInfoId: "",
                EmployeeInfoId: "",
                IssuedFrom: "",
                IssuedTO: "",
                ActualReturnDate: "",
                IssuedBy: "",
				};
	const [bookData, setCrud] = useState(initialState);
    // const [bookIssueData, setBookIssueData] = useState(bookIssueState);
    const [isDisabled, setIsDisabled] = useState(true);
    //const [isIssued, setIsIssued] = useState(true);
	const { BookInfoId } = useParams();
	const navigate = useNavigate();
    const [startDate, setStartDate] = useState();
    const [endDate, setEndDate] = useState();
    const handleFromDate = startDate => {
		setStartDate(startDate);
		bookIssueData.IssuedFrom=startDate.toISOString().slice(0, 10);
	}
	
	const isWeekStart = (startDate) => {

		let days=startDate.getDay();
		const day = days;
		return day !== 0 && day !== 6;
	  };
	  const isWeekEnd = (endDate) => {

		let days=endDate.getDay();
		const day = days;
		return day !== 0 && day !== 6;
	  };
	const handleToDate = endDate => {

		setEndDate(endDate);
		bookIssueData.IssuedTO=endDate.toISOString().slice(0, 10);
		bookIssueData.BookInfoId=BookInfoId;
		bookIssueData.EmployeeInfoId="2";
	}
	const [bookIssueData, setBookIssueData] = useState(bookIssueState);
	useEffect(
		function () {
			async function updateCrud() {
				try {
					const response = await get(`http://localhost:8080/api/books/${BookInfoId}`);
					console.log("response edit saagar",response)
					// const response = await get(`/api/cruds/${BookInfoId}`);
					setCrud(response.data[0]);
					// navigate("/bookInMyList");
					//setCrud([{"BookInfoId":1004,"BookName":"Wing Of Fire","Language":"English","BookType":"Pdf","Book_Auther":"Abdul Kalam","No_Copies_Actual":5,"No_Copies_Current":2,"Publication_year":1999,"BookCover":"test","BookDiscription":"Wings","BookStatus":"Availabe"}]);
				} catch (error) {
					console.log(error);
				}
			}
			updateCrud();
		},
		// eslint-disable-next-line react-hooks/exhaustive-deps
		[props]
	);

	function handleSubmit(event) {
        console.log("called sagar book data",bookData)
		event.preventDefault();
		async function issuedBook() {
			try {
				const response = await patch(`http://localhost:8080/api/bookIssue/${bookData.BookInfoId}`, bookData);
				//navigate("/bookInMyList");

				
			} catch (error) {
				console.log(error);
			}
		}
		issuedBook();
        saveIssuedBookInfo(event);
	}


    function saveIssuedBookInfo(event) {
       event.preventDefault();
   	//var string =myObjStr;
	console.log("json response",bookIssueData)

		async function saveIssuedBook() {
			try {
				const response = await post("http://localhost:8080/api/bookIssue",bookIssueData);
				console.log("response 2 sagar", response);
				navigate("/bookInMyList");
				// navigate(`/cruds/${response.data.BookInfoId}`);
			} catch (error) {
				console.log("error", error);
			}
		}
		saveIssuedBook();
	}




	function handleChange(event) {
		setCrud({ ...bookData, [event.target.name]: event.target.value });
        setBookIssueData({ ...bookIssueData, [event.target.name]: event.target.value });
		
	}

	function handleCancel() {
		// navigate(`/books/edit/${bookData.BookInfoId}`);
		navigate(`/books`);
	}

	return (
		<div className="container">
			<h1>Book : {bookData.BookName}</h1>
			<hr />
			<form onSubmit={handleSubmit}>
			<div className="form-group">
					<label>Book Name</label>
					<input
						name="BookName"
						type="text"
						required
						value={bookData.BookName}
						onChange={handleChange}
						className="form-control"
                        disabled={isDisabled}
					/>
				</div>
				<div className="form-group" disabled="true">
					<label>Language</label>
					<input
						name="Language"
						type="text"
						required
						value={bookData.Language}
						onChange={handleChange}
                        className="form-control"
                        disabled={isDisabled}
					/>
				</div>
				<div className="form-group" disabled="true">
					<label>Book Catagory</label>
					<input
						name="BookType"
						type="text"
						required
						value={bookData.BookType}
						onChange={handleChange}
                        disabled={isDisabled}
						className="form-control"
					/>
				</div>
				<div className="form-group" disabled="true">
					<label>Auther</label>
					<input
						name="BookAuther"
						type="text"
						required
						value={bookData.BookAuther}
						onChange={handleChange}
                        disabled={isDisabled}
                        className="form-control"
					/>
				</div>
				
				<div className="form-group" disabled="true">
					<label>Publication Year</label>
					<input
						name="PublicationYear"
						type="text"
						required
						value={bookData.PublicationYear}
						onChange={handleChange}
                        disabled={isDisabled}
						className="form-control"
					/>
				</div>
				
				<div className="form-group" disabled="true">
					<label>Description</label>
					<textarea
						name="BookDiscription"
						row="5"
						value={bookData.BookDiscription}
						onChange={handleChange}
                        disabled={isDisabled}
						className="form-control"
					/>
				</div>
                
							
				<div className="form-group" disabled="true">
					<label>ISBN</label>
					<input
						name="ISBN"
						type="text"
						required
						value={bookData.ISBN}
						onChange={handleChange}
                        disabled={isDisabled}
						className="form-control"
					/>
				</div>
                <div className="form-group">
					<label>Issued By</label>
					<input
						name="IssuedBy"
						type="text"
						required
						value={bookIssueData.IssuedBy}
					    	 
						onChange={handleChange}
                        className="form-control"
					/>
				</div>
                <div className="form-group" disabled="true">
                <label>Issue Date</label>
                {/* <DatePicker
   
	dateFormat="dd-MM-yyyy"
	placeholderText="Select Start Date"
	  value={startDate}
	 
      onChange={(date) => {
		const d = new Date(date).toISOString().slice(0, 10);
		console.log(d);
		
		setStartDate(bookIssueData.IssuedFrom=d);

	  }}
          
    /> */}
	<DatePicker selected={startDate}
	dateFormat="dd-MM-yyyy"
	minDate={new Date()}
	filterDate={isWeekStart}
	placeholderText="Select Start Date"
	onChange={handleFromDate} />
                 </div>
                 <div className="form-group">
                 <label>Return Date</label>
                

<DatePicker selected={endDate}
	dateFormat="dd-MM-yyyy"
	minDate={new Date()}
	filterDate={isWeekEnd}
	placeholderText="Select End Date"
	onChange={handleToDate} />
                </div>
				
				{/* <div className="form-group">
					<label>Website/Social Link</label>
					<input
						name="link"
						type="url"
						value={crud.link}
						onChange={handleChange}
						className="form-control"
					/>
					<small>Format: https://yourlink.ext</small>
				</div> */}

				
				<div className="btn-group">
					<button type="submit" className="btn btn-primary">
						Issue
										</button>
					
					<button
						type="button"
						onClick={handleCancel}
						className="btn btn-secondary"
					>
						Cancel
					</button>
				</div>
			</form>
		</div>
	);
}

export default BookIssueDetails;
