import React from 'react';
import './Login.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCoffee,faUser,faEnvelope,faCalendarDays,faBars,faLock } from '@fortawesome/free-solid-svg-icons'
import {Routes, Route, useNavigate} from 'react-router-dom';

function Login(){
    //redirect to login page
    const navigate = useNavigate();

    const registerForm = () => {
      navigate('/register');
    };

    return(
        <div className="" id="login">
          <div className="modal-dialog" style={{maxWidth:400}}>
            <div className="modal-content">   
              <div className="modal-header ">
                <h4 className="modal-title font_19">Login Form</h4>
                <button type="button" className="btn-close" data-bs-dismiss="modal"></button>
              </div>         
              <div className="modal-body">
              <form className='' > 
                <div className='row'>
                    <div className='col-md-12'>
                        <label className="control-label">User Name</label>
                            <div className="input-group mb-3">
                                <span className="input-group-text" id="basic-addon1"><FontAwesomeIcon icon={faUser} /></span>
                                <input type="text" className="form-control" placeholder="First Name"  />
                                <span></span>
                            </div>
                    </div>  
                    <div className='col-md-12'>
                        <label className='control-label'>Password</label>
                            <div className="input-group mb-3">
                                <span className="input-group-text" id="basic-addon1"><FontAwesomeIcon icon={faLock} /></span>
                                <input type="text" className="form-control" placeholder="Password" aria-label="Username" aria-describedby="basic-addon1" />
                            </div>    
                    </div>                                                           
                </div>            
                <div className='row'>
                    <h6 className='text-center text_blue register_text' onClick={registerForm}>New user? Register now.</h6>                  
                </div>  
                <button type="submit" className="btn btn-danger" data-bs-dismiss="modal">Save </button>        
                </form>
            </div>
        
             
           <div className="modal-footer">
               <button type="button" className="btn btn-danger" data-bs-dismiss="modal">Login </button>
                <button type="button" className="btn btn-danger" data-bs-dismiss="modal">Cancel</button>
              
            </div>
        
           </div>
          </div>
         </div>
    )
}
export default Login;