import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";


function BooksInmyList() {
	const [booksData, setCruds] = useState([]);

	useEffect(function () {
		async function getBooks() {
			try {
				const response = await axios.get("http://localhost:8080/api/booksInMyList");
				console.log("BooksUInMyList",response);
				setCruds(response.data);
				
			} catch (error) {
				console.log("error", error);
			}
		}
		getBooks();
	}, []);

	return (
		<div className="container">
			<div>
				<h2>
					Books - Table View
					{/* <p>
						<Link to="/book/new" className="btn btn-primary float-right">
							Create Book
						</Link>
					</p> */}
				</h2>
				<hr />
			</div>
		
                        <div className="table-responsive">
			<table className="table riped  table-hover table-bordered container">
				<thead>
					<tr>
						<th>Book&nbsp;Name</th>
						<th>Language</th>
						{/* <th>Book<br/>Type</th> */}
						<th>Book<br/>Auther</th>
						{/* <th>NoCopies<br/>Actual</th>
						<th>NoCopies<br/>Current</th>
						<th>Publication<br/>Year</th>
						<th>Book<br/>Cover</th> */}
						{/* <th>BookDiscription</th> */}
						{/* <th>Book<br/>Status</th>
						<th>ISBN</th> */}
						{/* <th>Issue</th> */}
						<th>Action</th>
						{/* <th>Delete</th> */}
					</tr>
				</thead>
				<tbody>
					{booksData &&
						booksData.map((item) => {
							return (
								<tr key={item.BookInfoId}>
									<td>
									{item.BookName}
										{/* <Link to={`/cruds/${crud._id}`} className="link-line">
											{crud.companyName}
										</Link> */}
									</td>
									<td>{item.Language}</td>
									{/* <td>{item.BookType}</td> */}
									<td>{item.BookAuther}</td>
									{/* <td>{item.NoCopiesActual}</td>
									<td>{item.NoCopiesCurrent}</td>
									<td>{item.PublicationYear}</td>
									<td>{item.BookCover}</td> */}
									{/* <td>{item.BookDiscription}</td> */}
									{/* <td>{item.BookStatus}</td>
									<td>{item.ISBN}</td> */}
									{/* <td>
										<Link to={`/issueDetails/${item.BookInfoId}`} className="btn btn-primary">
											Issue Book
										</Link>
									</td> */}
									{/* <td>
										<Link
											to={`/book/edit/${item.BookInfoId}`}
											className="btn btn-success"
										>
											Issue
										</Link>
									</td> */}
									<td>
										<Link
											to={`/bookReturn/${item.BookInfoId}`}
											className="btn btn-info"
										>
											Return
										</Link>
									</td>
								</tr>
							);
						})}
				</tbody>
			</table>
			</div>
		</div>
	);
}

export default BooksInmyList;
