import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';

function CrudTable() {
	const [booksData, setCruds] = useState([]);

	useEffect(function () {
		async function getBooks() {
			try {
				const response = await axios.get("http://localhost:8080/api/books");
				
				setCruds(response.data);
				
			} catch (error) {
				console.log("error", error);
			}
		}
		getBooks();
	}, []);

	

	return (
		<div className="container">
			<div>
				<h2>
					Books - Table View
					<p>
						<Link to="/book/new" className="btn btn-primary float-right">
							Create Book
						</Link>
					</p>
				</h2>
				<hr />
			</div>
		
                        <div className="table-responsive">
			<table className="table riped  table-hover table-bordered container">
				<thead>
					<tr>
						<th>BookName</th>
						<th>Language</th>
						<th>BookType</th>
						<th>BookAuther</th>
						<th>NoCopiesActual</th>
						<th>NoCopiesCurrent</th>
						<th>PublicationYear</th>
						<th>BookCover</th>
						<th>BookDiscription</th>
						<th>BookStatus</th>
						<th>ISBN</th>
						<th>View</th>
						<th>Edit</th>
						<th>Delete</th>
					</tr>
				</thead>
				<tbody>
					{booksData &&
						booksData.map((item) => {
							return (
								<tr key={item.BookInfoId}>
									<td>
									{item.BookName}
										{/* <Link to={`/cruds/${crud._id}`} className="link-line">
											{crud.companyName}
										</Link> */}
									</td>
									<td>{item.Language}</td>
									<td>{item.BookType}</td>
									<td>{item.BookAuther}</td>
									<td>{item.NoCopiesActual}</td>
									<td>{item.NoCopiesCurrent}</td>
									<td>{item.PublicationYear}</td>
									<td>{item.BookCover}</td>
									<td>{item.BookDiscription}</td>
									<td>{item.BookStatus}</td>
									<td>{item.ISBN}</td>
									<td>
										<Link to={`/books/${item.BookInfoId}`} className="btn btn-warning">
											View
										</Link>
									</td>
									<td>
										<Link
											to={`/book/edit/${item.BookInfoId}`}
											className="btn btn-success"
										>
											Edit
										</Link>
									</td>
									<td>
										<Link
											to={`/book/delete/${item.BookInfoId}`}
											className="btn btn-danger"
										>
											Delete
										</Link>
									</td>
								</tr>
							);
						})}
				</tbody>
			</table>
			</div>
		</div>
	);
}

export default CrudTable;
