import React, { useState } from "react";
import { post } from "axios";
import { useNavigate } from "react-router-dom";
import 'react-dropdown/style.css';
import Select from 'react-select';
import 'bootstrap/dist/css/bootstrap.min.css';
import DatePicker  from "react-datepicker";  
import "react-datepicker/dist/react-datepicker.css";  




function Register(props) {

	const initialState = {
		FirstName: "",
		LastName: "",
		Gender: "",
	    Email: "",
		DOB: "",
		Designation: "",
		Password: "",
		IsAdmin: "",
		};
	const gender = [
		{ label: "Male", value: 1 },
        { label: "Female", value: 2 },
        { label: "Other", value: 3 },
		// { label: "Not Available", value: 2 },
	  ];

      const isAdmin = [
		{ label: "Admin", value: 1 },
        { label: "Employee", value: 0 },
         ];
    const [birthDate, setDOB] = useState();
	const [registerData, setRegister] = useState(initialState);
	const navigate = useNavigate();
	function handleListChange (selectedOption) {
		registerData.IsAdmin=selectedOption.value;
			  }
              function handleGenderChange (selectedOption) {
                registerData.Gender=selectedOption.label;
                      }
              
              const handleDOB = birthDate => {
                setDOB(birthDate);
                registerData.DOB=birthDate.toISOString().slice(0, 10);
            }
	function handleSubmit(event) {
		event.preventDefault();
		console.log("from api/cruds register",registerData)
		//if (!crud.companyName || !crud.email) return;
		async function saveBook() {
			try {
				const response = await post("http://localhost:8080/api/register",registerData);
				console.log("response 2 sagar", response);
				navigate("/login");
				// navigate(`/cruds/${response.data.BookInfoId}`);
			} catch (error) {
				console.log("error", error);
			}
		}
		saveBook();
	}

	
	function handleChange(event) {
		setRegister({ ...registerData, [event.target.name]: event.target.value });
		
	}

	function handleCancel() {
		navigate("/books");
	}

	
	
	return (
		<div className="container" style={{ maxWidth: "400px" }}>
			<h1>Add Book</h1>
			<hr />
			<form onSubmit={handleSubmit}>
			
				<div className="form-group">
					<label>First Name</label>
					<input
						name="FirstName"
						type="text"
						required
						value={registerData.FirstName}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Last Name</label>
					<input
						name="LastName"
						type="text"
						required
						value={registerData.LastName}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Email</label>
					<input
						name="Email"
						type="text"
						required
						value={registerData.Email}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				{/* <div className="form-group">
					<label>Date Of Birth</label>
					<input
						name="DOB"
						type="text"
						required
						value={registerData.DOB}
						onChange={handleChange}
						className="form-control"
					/>
				</div> */}
                <div className="form-group">
                 <label>Date Of Birth</label>
                

<DatePicker selected={birthDate}
	dateFormat="dd-MM-yyyy"
	placeholderText="Select End Date"
	onChange={handleDOB} />
                </div>
				<div className="form-group">
					<label>Designation</label>
					<input
						name="Designation"
						type="text"
						required
						value={registerData.Designation}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Password</label>
					<input
						name="Password"
						type="text"
						required
						value={registerData.Password}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
			   <div className="form-group">
					<label>Gender</label>
				     <Select options={ gender } 
					    name="gender"
					  	required
					    onChange={handleGenderChange}
      					/>
				</div> 
                <div className="form-group">
					<label>Admin/Emp</label>
				     <Select options={ isAdmin } 
					    name="isAdmin"
					  	required
					    onChange={handleListChange}
      					/>
				</div> 
				
				<div className="btn-group">
					<input type="submit" value="Submit" className="btn btn-primary" />
					<button
						type="button"
						onClick={handleCancel}
						className="btn btn-secondary"
					>
						Cancel
					</button>
				</div>
			</form>
		</div>



	);
}

export default Register;
