import React, { useState, useEffect } from "react";
import { get, patch } from "axios";
import { useNavigate, useParams } from "react-router-dom";
import Select from 'react-select';
import 'bootstrap/dist/css/bootstrap.min.css';

function CrudEdit(props) {
	const initialState = {
		BookName: "",
		Language: "",
		BookType: "",
	    BookAuther: "",
		NoCopiesActual: "",
		NoCopiesCurrent: "",
		PublicationYear: "",
		BookCover: "",
		BookDiscription: "",
		BookStatus: "",
		ISBN: "",
			};
	const [bookData, setCrud] = useState(initialState);
	const { BookInfoId } = useParams();
	const navigate = useNavigate();
	const bookStatus = [
		{ label: "Available", value: 1 },
	    { label: "Not Available", value: 2 },
	  ];

	useEffect(
		function () {
			async function updateCrud() {
				try {
					const response = await get(`http://localhost:8080/api/books/${BookInfoId}`);
					console.log("response edit saagar",response)
					// const response = await get(`/api/cruds/${BookInfoId}`);
					setCrud(response.data[0]);
					//setCrud([{"BookInfoId":1004,"BookName":"Wing Of Fire","Language":"English","BookType":"Pdf","Book_Auther":"Abdul Kalam","No_Copies_Actual":5,"No_Copies_Current":2,"Publication_year":1999,"BookCover":"test","BookDiscription":"Wings","BookStatus":"Availabe"}]);
				} catch (error) {
					console.log(error);
				}
			}
			updateCrud();
		},
		// eslint-disable-next-line react-hooks/exhaustive-deps
		[props]
	);
	function handleListChange (selectedOption) {
		bookData.BookStatus=selectedOption.label;
			  }
	function handleSubmit(event) {
		event.preventDefault();
		async function updateBook() {
			try {
				const response = await patch(`http://localhost:8080/api/book/${bookData.BookInfoId}`, bookData);
				console.log("Edit response",response);
				navigate(`/books/${bookData.BookInfoId}`);
			} catch (error) {
				console.log(error);
			}
		}
		updateBook();
	}

	function handleChange(event) {
		setCrud({ ...bookData, [event.target.name]: event.target.value });
	}

	function handleCancel() {
		// navigate(`/books/edit/${bookData.BookInfoId}`);
		navigate(`/books`);
	}

	return (
		<div className="container">
			<h1>Edit {bookData.BookName}</h1>
			<hr />
			<form onSubmit={handleSubmit}>
			<div className="form-group">
					<label>Book Name</label>
					<input
						name="BookName"
						type="text"
						required
						value={bookData.BookName}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Language</label>
					<input
						name="Language"
						type="text"
						required
						value={bookData.Language}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>BookType</label>
					<input
						name="BookType"
						type="text"
						required
						value={bookData.BookType}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Auther</label>
					<input
						name="BookAuther"
						type="text"
						required
						value={bookData.BookAuther}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Number Of Copies Actual</label>
					<input
						name="NoCopiesActual"
						type="text"
						required
						value={bookData.NoCopiesActual}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Numbber Of Copies Current</label>
					<input
						name="NoCopiesCurrent"
						type="text"
						required
						value={bookData.NoCopiesCurrent}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Publication Year</label>
					<input
						name="PublicationYear"
						type="text"
						required
						value={bookData.PublicationYear}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Book Cover</label>
					<input
						name="BookCover"
						type="text"
						required
						value={bookData.BookCover}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				<div className="form-group">
					<label>Description</label>
					<textarea
						name="BookDiscription"
						row="5"
						value={bookData.BookDiscription}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				{/* <div className="form-group">
					<label>Book Status</label>
					<input
						name="BookStatus"
						type="text"
						required
						value={bookData.BookStatus}
						onChange={handleChange}
						className="form-control"
					/>
				</div> */}
						<div className="form-group">
					<label>Book Status</label>
					   <Select options={ bookStatus } 
					    name="BookStatus"
					  	required
					    onChange={handleListChange}
      					/>
					</div> 
				<div className="form-group">
					<label>ISBN</label>
					<input
						name="ISBN"
						type="text"
						required
						value={bookData.ISBN}
						onChange={handleChange}
						className="form-control"
					/>
				</div>
				
				{/* <div className="form-group">
					<label>Website/Social Link</label>
					<input
						name="link"
						type="url"
						value={crud.link}
						onChange={handleChange}
						className="form-control"
					/>
					<small>Format: https://yourlink.ext</small>
				</div> */}

				
				<div className="btn-group">
					<button type="submit" className="btn btn-primary">
						Update
					</button>
					<button
						type="button"
						onClick={handleCancel}
						className="btn btn-secondary"
					>
						Cancel
					</button>
				</div>
			</form>
		</div>
	);
}

export default CrudEdit;
