import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { text } from "@fortawesome/fontawesome-svg-core";


function CrudTable() {
	const [booksData, setCruds] = useState([]);
	const [empData, setEmpData] = useState([]);
	useEffect(function () {
		async function getBooks() {
			try {
				const response = await axios.get(process.env.ALVEOLIBRARY_APP_API_ENDPOINT);
				
				setCruds(response.data);
							
			} catch (error) {
				console.log("error", error);
			}
		}
		getBooks();
		}, []);
	
	useEffect(function () {
		async function getEmpName() {
			try {
				const response = await axios.get("http://localhost:8080/api/getEmpName");
				
				setEmpData(response.data);
				console.log(response.data)
							
			} catch (error) {
				console.log("error", error);
			}
		}
		getEmpName();
	}, []);
	
	// Match employee name with Book id which employee issued that book
	let merged = [];

	for(let i=0; i<booksData.length; i++) {
	  merged.push({
	   ...booksData[i], 
	   ...(empData.find((itmInner) => itmInner.BookInfoId === booksData[i].BookInfoId))}
	  );
	}
	
	console.log(merged);
   	return (
		<div className="container">
			<div>
				<h2>
					Books - Table View
					{/* <p>
						<Link to="/book/new" className="btn btn-primary float-right">
							Create Book
						</Link>
					</p> */}
				</h2>
				<hr />
			</div>
		
                        <div className="table-responsive">
			<table className="table riped  table-hover table-bordered container">
				<thead>
					<tr>
						<th>Book&nbsp;Name</th>
						<th>Language</th>
						<th>Book<br/>Type</th>
						<th>Book<br/>Auther</th>
						<th>NoCopies<br/>Actual</th>
						<th>NoCopies<br/>Current</th>
						<th>Publication<br/>Year</th>
						<th>Book<br/>Cover</th>
						{/* <th>BookDiscription</th> */}
						<th>Book<br/>Status</th>
						<th>ISBN</th>
						<th>Action</th>
						{/* <th>Return</th> */}
						{/* <th>Delete</th> */}
					</tr>
				</thead>
				<tbody>
					{	merged &&
						merged.map((item) => {
							return (
								<tr key={item.BookInfoId}>
									<td>
									{item.BookName}
										{/* <Link to={`/cruds/${crud._id}`} className="link-line">
											{crud.companyName}
										</Link> */}
									</td>
									<td>{item.Language}</td>
									<td>{item.BookType}</td>
									<td>{item.BookAuther}</td>
									<td>{item.NoCopiesActual}</td>
									<td>{item.NoCopiesCurrent}</td>
									<td>{item.PublicationYear}</td>
									<td>{item.BookCover}</td>
									{/* <td>{item.BookDiscription}</td> */}
									<td><span style={item.BookStatus==="Available" ? null:{pointerEvents: "none" , color: 'red'} }>{item.BookStatus}</span><br/><span  style={{ color: 'blue' ,fontWeight: '300',fontSize: '12px'}}>{item.FirstName} {item.LastName}</span></td>
									<td>{item.ISBN}</td>
									<td>										
										<Link to={`/issueDetails/${item.BookInfoId}`}  className="btn btn-primary" style={item.BookStatus==="Available" ? null:{pointerEvents: "none" ,background: 'red'} }>
											Issue Book
										</Link>										
									</td>
									{/* <td>
										<Link
											to={`/book/edit/${item.BookInfoId}`}
											className="btn btn-success"
										>
											Issue
										</Link>
									</td> */}
									{/* <td>
										<Link
											to={`/issueDetails/l${item.BookInfoId}`}
											className="btn btn-info"
										>
											Return
										</Link>
									</td> */}
								</tr>
							);
						})}
				</tbody>
			</table>
			</div>
		</div>
	);
}

export default CrudTable;
