import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link, useNavigate, useParams } from "react-router-dom";

function BookDetails(props) {
	const [book, setCrud] = useState({});
	//const { BookInfoId } = this.props.match.params;
	
	const { BookInfoId } = useParams();
	console.log("BookInfoId",BookInfoId);
	const navigate = useNavigate();

	useEffect(
		function () {
			async function getBookById() {
				try {
					const response = await axios.get(`http://localhost:8080/api/books/${BookInfoId}`);
					console.log("response Details",response);
					setCrud(response.data[0]);
				} catch (error) {
					console.log("error", error);
				}
			}
			getBookById();
		},
		// eslint-disable-next-line react-hooks/exhaustive-deps
		[props]
	);

	async function handleDelete() {
		try {
			await axios.delete(`/api/books/${BookInfoId}`);
			navigate("/cruds");
		} catch (error) {
			console.error(error);
		}
	}

	return (
		<div className="container">
			<h2>{book.BookName}</h2>
			
			<p>
				<b>Language</b>:  {book.Language} 
			</p>

			<p>
				<b>BookType</b>: {book.BookType}
			</p>
			<p>
				<b>BookAuther</b>: {book.BookAuther}
			</p>
			<p>
				<b>Number Of Copies Actual</b>: {book.NoCopiesActual}
			</p>
			<p>
				<b>Number Of Copies Current</b>: {book.NoCopiesCurrent}
			</p>
			<p>
				<b>Publication Year</b>: {book.PublicationYear}
			</p>
			<p>
				<b>BookCover</b>: {book.BookCover}
			</p>
			<p>
				<b>Book Discription</b>: {book.BookDiscription}
			</p>
			<p>
				<b>Book Status</b>: {book.BookStatus}
			</p>
			<p>
				<b>ISBN</b>: {book.ISBN}
			</p>
			
			{/* <p>
				<b>Link</b> :
				<a href={` ${book.link}`} target="_blank" rel="noreferrer">
					{book.link}
				</a>
			</p> */}
			<p>
				<b>Description</b>: <p align="justify">{book.BookDiscription}</p>
			</p>
			<p>
				<small>
					<b>ID</b>: {book.BookInfoId}
				</small>
			</p>
			<div className="btn-group ">
				<Link to={`/book/edit/${book.BookInfoId}`} className="btn btn-primary">
					Edit
				</Link>
				<button onClick={handleDelete} className="btn btn-danger">
					Delete
				</button>
				<Link to="/books" className="btn btn-secondary">
					Close
				</Link>
			</div>
			<hr />
		</div>
	);
}

export default BookDetails;
